  font-size: 24px;
}
.startup .items-wrapper {
  padding-left: 60px;
  padding-right: 60px;
  margin-bottom: 10px;
}
.startup .items-wrapper .items-header {
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #ecf0f5;
  margin-bottom: 25px;
}
.startup .items-wrapper .items-header h3 {
  margin-bottom: 0px;
  font-size: 15px;
  color: #00000069;
}
.startup .items-wrapper .my-item {
  margin-bottom: 10px;
}
.startup .items-wrapper .my-item .description span {
  font-size: 14px;
  color: #879db7;
}
.startup .items-wrapper .my-item .description h3 {
  color: #00000069;
  font-size: 18px;
}
.startup .items-wrapper .my-item .vat p {
  color: #879db7;
}
.startup .items-wrapper .my-item .total p {
  font-weight: bold;
}
.calculation {
  margin-bottom: 30px;
}
.calculation .result-wrapper {
  padding-right: 60px;
}
.calculation .result-wrapper .result {
  padding: 20px;
  background-color: #ecf0f5;
}
.calculation .result-wrapper .result .op p {
  font-size: 18px;
  color: #879db7;
  margin-bottom: 0px;
}
.calculation .result-wrapper .result .bl h2 {
  font-weight: bold;
  margin-bottom: 0px;
}
.footer {
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #c9d2dc;
}
p {
  word-break: break-all;
}
.ant-select-dropdown {
  position: relative !important;
}
.ant-select.ant-select-enabled ~ div {
  position: relative !important;
}

.local-upload-outer .local-upload-preview {
  border-radius: 10px;
  border: 1.6px dashed #0053FA;
  overflow: hidden;
  background: #FFFFFF;
  width: 100%;
  min-height: 150px;
  position: relative;
  margin-bottom: 24px;
}
.local-upload-outer .local-upload-preview .preview-content {
  overflow: hidden;
  width: 100%;
  position: relative;
}
.local-upload-outer .local-upload-preview .preview-content > * {
  width: 100%;
}
.local-upload-outer .local-upload-preview .local-upload-preview-close {
  position: absolute;
  z-index: 9;
  top: 0;
  right: 0px;
  width: 25px;
  height: 25px;
  padding: 8px;
  cursor: pointer;
}
.local-upload-outer .local-upload-wrapper {
  width: 100%;
}

