.upload-component .btn-logo-add {
  border-width: 0px;
  box-shadow: 0px 10px 10px 0px #e8e8e8;
}
.upload-component .ant-upload.ant-upload-drag,
.upload-component .drop-zone {
  margin: 2em 0 !important;
  border-radius: 16px !important;
  padding: 2em 1em !important;
}
.upload-component .upload-icon {
  width: 50px;
  height: 50px;
}
.upload-component .drop-title {
  color: #0053FA;
  margin-top: 1.3em;
  font-weight: 500;
  font-size: 1.2em;
  margin-bottom: 0.3em;
}
.upload-component .drop-description {
  color: #AEAEAE;
}
.upload-component .description {
  margin: 2em 0;
  font-size: 0.8em;
}
.invocing-carousel {
  height: 392px;
}
.invoicing-select {
  width: 100%;
  position: relative;
}
.invoicing-select .ant-select {
  width: 100%;
}
.invoicing-select .ant-select .ant-select-selection--single {
  height: 48px;
  border-color: #AEAEAE;
}
.invoicing-select .ant-select .ant-select-selection--single .ant-select-selection__rendered {
  font-size: 1.3em;
}
.invoicing-select .ant-input {
  height: 48px;
}
.invoicing-select .invoicing-element-title {
  text-align: left;
  text-transform: uppercase;
  font-size: 14px;
}
.invoicing-select.mt-4 {
  margin-top: 4em;
}
.invoicing-detail-body-footer {
  width: 100%;
  position: relative;
}
.invoicing-detail-body-footer .discount,
.invoicing-detail-body-footer .total-not-vat,
.invoicing-detail-body-footer .total-vat {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
}
.invoicing-detail-body-footer .discount {
  margin-bottom: 4em;
  white-space: nowrap;
}
.invoicing-detail-body-footer .discount .ant-input {
  width: 30%;
}
.invoicing-detail-body-footer .total-not-vat {
  margin-bottom: 1em;
}
.invoicing-detail-body-footer .total-vat {
  font-weight: bolder;
}
.text-left {
  text-align: left;
}
.text-right {
  text-align: right;
}
.invoicing-item-body {
  margin: 1em 0;
}
.invoicing-item-body .ant-card-body {
  padding: 18px 24px;
  position: relative;
}
.invoicing-item-body .item-trash {
  position: absolute;
  right: 1em;
  top: 5px;
}
.invoicing-item-body .ant-row {
  width: 100%;
  display: flex;
  align-items: center;
}
.invoicing-item-body .item-title {
  margin: 0;
  font-weight: bolder;
}
.invoicing-item-body .item-description {
  margin: 0;
}
.invoicing-item-body .item-result {
  margin: 0;
}
.invoicing-input {
  width: 100%;
}
.invoicing-input .ant-input {
  height: 48px;
  border-color: #AEAEAE;
}
.invoicing-input .invoicing-element-title {
  text-align: left;
  text-transform: uppercase;
  font-size: 14px;
}
.invoicing-input .invoicing-element-title.txt-area {
  margin-bottom: 10px;
}
.invoicing-element-title span {
  color: #AEAEAE;
  font-size: 13px;
}
.mr-5p {
  margin-right: 5px;
}
.ant-input-affix-wrapper .ant-input:not(:last-child) {
  padding-right: 0px;
}
